.footerContact {
  background-color: #27ae60;
  padding: 40px 0;
  color: #fff;
}
.footerContact h1 {
  font-size: 40px;
}
footer {
  background-color: #1d2636;
  padding: 50px 0;
  color: #fff;
}
footer .container {
  display: grid;
  grid-template-columns: 6fr 2fr 2fr 1fr;
  grid-gap: 20px;
}
footer img {
  width: 150px;
  margin-bottom: 1em;
}
footer h2 {
  font-weight: 500;
}
footer p {
  color: grey;
  margin: 20px 0;
}
footer input {
  background-color: #fff;
  padding: 17px;
  width: 100%;
  border-radius: 5px;
}
footer h3 {
  font-weight: 500;
  margin-bottom: 30px;
}
footer ul {
  display: block;
}
footer ul li {
  display: block;
  color: grey;
  margin-bottom: 20px;
}
.legal {
  text-align: center;
  padding: 20px;
  background: #1d2636;
  color: rgba(255, 255, 255, 0.5);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}
@media screen and (max-width: 800px) {
  footer .container {
    grid-template-columns: 2fr 1fr;
    grid-column-gap: 12em;
  }
  .footerContact h1 {
    font-size: 30px;
  }
}
@media screen and (max-width: 500px) {
  .box2 {
    display: none;
  }
  footer .container {
    grid-template-columns: 2fr;
  }
  .footerContact h1 {
    font-size: 22px;
  }
  .footerContact button {
    width: 35%;
    height: 20%;
    padding: 0.7em;
    font-size: 1em;
    border-radius: 0.6em;
  }
}
